<template>
    <div>
<h3>登录页面</h3>
<div>
<input type="text" v-model="name" />
</div>
<div>
<input type="text" v-model="pwd" />
</div>
<div>
<button @click="handLogin" type="submit">登录</button>
</div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                name: '',
                pwd: ''
            }
        },
        methods: {
            handLogin(){
                setTimeout(() =>{
                    let data = {
                        name:this.name
                    }
                    localStorage.setItem('user',JSON.stringify(data));
                    this.$router.push({
                        //path:'/notes'
                        // name:'notes'
                        path: this.$route.query.redirect
                        })
                    },3000);
            }
        },
    }
</script>

